___CSS_LOADER_EXPORT___.push([module.id, `/* Stylingtampilan layar kecil */

@media only screen and (min-width: 650px) {
    .navbar {
      grid-template-columns: 1fr auto;
      padding: 10px 15px;
    }
    
      .navbar .navbar_menu {
        display: none;
      }
    
      .navbar .navbar_navigation {
        position: static;
        width: 100%;
      }
    
      .navbar .navbar_navigation ul li {
        display: inline-flex;
        flex-direction: row;
      }
    
      .navbar .navbar_navigation ul li a {
        display: inline-block;
        height: 44px;
        /* padding: -10px; */
        text-align: center;
        /* margin-bottom: 44px; */
      }
  
      .navbar .navbar_brand h1 {
        font-size: 1.5em;
    }
  }

  /* Styling untuk tampilan layar sedang */
  @media only screen and (min-width: 651px) and (max-width: 1024px) {
    .list {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
    .favorite {
      display: inline-block;
      grid-template-columns: 1fr 1fr;
    }
  }

  @media only screen and (min-width: 750px) {
    .list {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .favorite {
      display: inline-block;
      grid-template-columns: 1fr 1fr;
    }
}

  @media only screen and (min-width: 950px) {
      .list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr
      }
      .favorite {
        display: inline-block;
        grid-template-columns: 1fr 1fr 1fr
      }
  }

  @media only screen and (min-width: 1200px) {
      main {
          max-width: 1200px;
      }
  }`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA,gCAAgC;;AAEhC;IACI;MACE,+BAA+B;MAC/B,kBAAkB;IACpB;;MAEE;QACE,aAAa;MACf;;MAEA;QACE,gBAAgB;QAChB,WAAW;MACb;;MAEA;QACE,oBAAoB;QACpB,mBAAmB;MACrB;;MAEA;QACE,qBAAqB;QACrB,YAAY;QACZ,oBAAoB;QACpB,kBAAkB;QAClB,yBAAyB;MAC3B;;MAEA;QACE,gBAAgB;IACpB;EACF;;EAEA,wCAAwC;EACxC;IACE;MACE,aAAa;MACb,8BAA8B;IAChC;IACA;MACE,qBAAqB;MACrB,8BAA8B;IAChC;EACF;;EAEA;IACE;QACI,aAAa;QACb,8BAA8B;IAClC;IACA;MACE,qBAAqB;MACrB,8BAA8B;IAChC;AACJ;;EAEE;MACI;QACE,aAAa;QACb;MACF;MACA;QACE,qBAAqB;QACrB;MACF;EACJ;;EAEA;MACI;UACI,iBAAiB;MACrB;EACJ","sourcesContent":["/* Stylingtampilan layar kecil */\r\n\r\n@media only screen and (min-width: 650px) {\r\n    .navbar {\r\n      grid-template-columns: 1fr auto;\r\n      padding: 10px 15px;\r\n    }\r\n    \r\n      .navbar .navbar_menu {\r\n        display: none;\r\n      }\r\n    \r\n      .navbar .navbar_navigation {\r\n        position: static;\r\n        width: 100%;\r\n      }\r\n    \r\n      .navbar .navbar_navigation ul li {\r\n        display: inline-flex;\r\n        flex-direction: row;\r\n      }\r\n    \r\n      .navbar .navbar_navigation ul li a {\r\n        display: inline-block;\r\n        height: 44px;\r\n        /* padding: -10px; */\r\n        text-align: center;\r\n        /* margin-bottom: 44px; */\r\n      }\r\n  \r\n      .navbar .navbar_brand h1 {\r\n        font-size: 1.5em;\r\n    }\r\n  }\r\n\r\n  /* Styling untuk tampilan layar sedang */\r\n  @media only screen and (min-width: 651px) and (max-width: 1024px) {\r\n    .list {\r\n      display: grid;\r\n      grid-template-columns: 1fr 1fr;\r\n    }\r\n    .favorite {\r\n      display: inline-block;\r\n      grid-template-columns: 1fr 1fr;\r\n    }\r\n  }\r\n\r\n  @media only screen and (min-width: 750px) {\r\n    .list {\r\n        display: grid;\r\n        grid-template-columns: 1fr 1fr;\r\n    }\r\n    .favorite {\r\n      display: inline-block;\r\n      grid-template-columns: 1fr